#parentbox {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
  /* margin: 10rem; */
}

.align-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
}

#heading {
  font-family: Montserrat;
  font-variant: normal;
  font-size: 4.5vh;
  font-weight: 900;
  text-align: center;
}

#title {
  font-family: Montserrat;
  font-variant: normal;
  font-size: 2vh;
  font-weight: 900;
  text-align: center;
}

#label-text {
  font-family: Montserrat;
  font-variant: normal;
  font-size: 2vh;
  font-weight: 900;
  text-align: left;
}

.filter-box {
  display: flex;
  align-content: flex-end;
  justify-content: flex-end;
  /* text-align: end; */
  margin: 1rem;
  width: 55%;
  margin-right: 10%;
  margin-left: 10%;
  max-height: 2.5rem;
}
.msg-box {
  display: flex;
  align-content: flex-end;
  justify-content: flex-end;
  max-height: max-content;
}

.filter-button {
  height: 2.5rem;
  margin: 1rem;
}

#filter-icon {
  width: 1.5rem;
  margin-right: 0.5rem;
}

#data-table {
  height: 50vh;
  min-width: 500px;
}

.table-header {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: bold;
}

.table-data {
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 500;
}
