.card-label {
  font-size: 14px;
  /* font-style: normal; */
  font-weight: 500;
  /* line-height: normal; */
}

.tags-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  row-gap: 5px;
  column-gap: 5px;
}
.station-tag {
  /*Layout*/
  display: inline-flex;
  padding: 5px 15px;
  justify-content: center;
  align-items: center;
  gap: 10px;

  border-radius: 10px;
  background: #b3d5ee; /* var(--secondary-colors-light-grey, #dbdbdb); */

  /*Text*/
  /* color: var(--secondary-colors-dark-gray, #727a89); */
  font-family: Open Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.station-destination {
  display: flex;
  padding: 0.5rem 2rem;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 10px;
  background: #b3d5ee;
  color: var(--secondary-colors-blue, #0071c5);
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.card-close-btn {
  padding: 0;
  transform: scale(0.75);
}
.station-card-container {
  z-index: 1;
  position: absolute;
}
.station-card {
  border-radius: 10px !important;
  background: #f5f9ff !important;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.5) !important;
