.message-box,
.hide-message-box {
  display: flex;
  padding: 11px 50px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 10px;
  background: #ff4136;
  position: fixed;
  width: 35%;
  opacity: 1;
  color: var(--primary-colors-seasalt, #fafafa);
  font-size: 12px;
  font-family: Open Sans;
  font-weight: 500;
}

.hide-message-box {
  color: #ff4136;
}

.message-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 1.5rem;
}
