.data-grid {
  width: 100%;
  min-height: 150;
  line-height: 3rem;

  position: relative;
  display: flex;
  margin-bottom: 1rem;
}
.main-box {
  width: 100%;
}
.alignCenter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cell-style {
  white-space: normal;
}

.no-events {
  pointer-events: none;
}

.column-button {
  margin-left: -10px;
  height: 2rem;
  padding: 0.1rem;
}
.data-grid-box {
  margin-left: 1rem;
  margin-right: 1rem;
  height: 50vh;
}
