.tooltips {
  position: absolute;
  text-align: left;
  width: auto;
  height: auto;
  display: flex;
  padding: 10px 15px;
  flex-direction: column;
  align-items: flex-start;
  border-radius: 10px;
  background: #afd1ea;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  pointer-events: none;
  color: var(--primary-colors-jet-black, #0a0a0a);
  font-family: Open Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  /* padding: 10px;
  line-height: 18px;
  font: 12px sans-serif;
  background: lightsteelblue;
  border: 0px;
  border-radius: 8px;
  pointer-events: none; */
}
.tooltips-heading {
  color: var(--primary-colors-jet-black, #0a0a0a);
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: bold;
  line-height: 16px;
  text-transform: uppercase;
}
.tooltips em {
  font-style: normal;
}
