.thumbViewClass {
  /* border: 1px solid grey; */
  position: absolute;
  bottom: 5px;
  width: 15%;
  height: 25vh;
  margin: 3px;
  padding: 0px;
  overflow: hidden;
  top: 15%;
  left: 10%;
  border-radius: 5px;
}

#thumbView {
  z-index: 140;
  background-color: gray;
  opacity: 0.5;
}

#scopeContainer {
  z-index: 150;
}

#scope {
  z-index: 150;
  cursor: move;
}

#thumbMap {
  padding-right: 10px !important;
}

#mydiv {
  position: absolute;
  z-index: 9;
  background-color: #f1f1f1;
  border: 1px solid #d3d3d3;
  text-align: center;
  width: auto;
  height: auto;
}

#mydivheader {
  padding: 10px;
  cursor: move;
  background-color: #2196f3;
  color: #fff;
}

