.diagnostics-img-collage-button-small {
  border: none;
}

.diagnostics-img-collage-button-small:hover {
  border: none;
  box-shadow: none;
  cursor: zoom-in;
}

.diagnostics-img-collage-button-full:hover {
  cursor: zoom-out;
}

.diagnostics-img-collage-small {
  width: 600px;
  height: 400px;
}

.diagnostics-img-container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  max-height: 60vh;
}

.diagnostics-img-collage-full {
  width: 1200px;
  height: 1200px;
}

.diagnostics-more-data-btn {
  background-color: #ffffff;
  border: 2px solid #4c608d;
  color: #012169;
}

.diagnostics-more-data-btn:hover {
  background-color: #ffffff;
  cursor: pointer;
}

.diagnostics-no-more-data-btn {
  background-color: #cfd2cf;
  color: #748da6;
  font-weight: 500;
}

.diagnostics-no-more-data-btn:hover {
  background-color: #cfd2cf;
  cursor: default;
}

.diagnostics-title-diag {
  font-weight: 600;
  padding-left: 5vw;
  height: 6vh;
}

.diagnostics-get-data-button {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 3rem;
}

.diagnostics-data-grid {
  border-left: 1px solid gray;
  padding-left: 1rem;
}

.diagnostics-data-grid-key {
  display: inline;
  font-weight: 600;
}

.diagnostics-data-grid-value {
  display: inline;
  margin-left: 1vh;
}

.diagnostics-header {
  padding-left: 0px;
  display: flex;
}
