.action-request-item {
  border-radius: 10px;
  background: #fff;
}
.action-request-alert-title {
  color: #0a0a0a;
  font-family: Montserrat;
  font-size: 20px !important;
  font-style: normal;
  font-weight: 700 !important;
  line-height: normal;
}
.action-request-clear-btn {
  border-radius: 10px;
  border: 1.5px solid var(--secondary-colors-blue, #0071c5);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  padding: 5px 24px !important;
}
.clear-all-link {
  color: var(--secondary-colors-blue, #0071c5);
  font-family: Open Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration-line: underline;
  cursor: pointer;
}
.ar-dialog-actions {
  display: flex;
  justify-content: right;
}
