.hr-theme-slash-2 {
  display: flex;

  .hr-line {
    width: 100%;
    position: relative;
    margin: 15px;
    border-bottom: 1px solid #000;
  }
  .hr-icon {
    position: relative;
    top: 3px;
    color: #ff0000;
  }
}

/* Container holding the image and the text */
.container {
  position: relative;
  justify-content: center;
  align-items: center;
}

.route-text {
  font-weight: bold;
  font-size: 0.8rem;
  margin-left: 0.25rem;
  display: inline-block;
  width: 9rem;
  vertical-align: top;
}
.fullwidth-box {
  width: 100%;
}

#station-icon {
  height: 4rem;
  width: auto;
  cursor: pointer;
  /* colorRendering: auto; */
  rotate: 270deg;
}

.route-line {
  position: absolute;
  width: 65px;
  height: 0px;
  top: 15%;
  margin-left: 1.5rem;
}

.row-box {
  display: flex;
  direction: row;
  align-items: center;
}

.back-button {
  font-weight: 800;
  cursor: pointer;
}

.title-box {
  width: 80%;
  font-weight: bold;
  text-align: center;
  align-content: center;
  justify-content: center;
}

#schedule-button {
  padding: 0.825rem;
  margin-left: 1rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  width: 11rem;
}

#tags-button {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  /* margin-right: 1rem; */
  font-weight: bold;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
  width: 11rem;
}

#route-box {
  width: 100%;
  min-height: 12vh;
  border: 1px solid #000000;
  border-radius: 1em;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
  direction: row;
}

#showall {
  position: absolute;
  left: 85%;
  padding: 0 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  background-color: #ffffff;
  /* identical to box height */
  text-decoration: underline;
  color: #0000ff;
  cursor: pointer;
}

#route-container {
  position: relative;
  display: flex;
  justify-content: flex-start;
  width: 100%;
  flex-wrap: wrap;
  padding-bottom: 2vh;
  margin-left: 10px;
}

#search-textfield {
  width: 100%;
}

#stations-box {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  row-gap: 0.5rem;
  justify-content: center;
}
#button-box {
  display: flex;
  direction: row;
  margin-top: 2rem;
  margin-bottom: 7rem;
}

.page-button {
  padding-top: 1em;
  margin-left: 1rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  width: 10rem;
  height: 3rem;
}

/* Bottom right text */
.text-block {
  position: absolute;
  bottom: 1%;
  right: 57%;
  top: 60%;
  justify-content: center;
  /* padding-left: 20px;
  padding-right: 20px; */
}

.vertical-center {
  margin: 0;
  position: absolute;
  top: 0%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.iconwrapper {
  text-align: center;
  display: inline-block;
}
.iconwrapper::before,
.iconwrapper::after {
  content: '';
  display: inline-block;
  width: 3em;
  height: 4px;
  background: black;
  vertical-align: middle;
}

.vertical-line {
  width: 4rem;
  height: 0px;
  color: #add8e6;
}

.saveCheck {
  display: inline;
  margin-right: 1vw;
}

.book {
  width: 8rem;
  font-weight: bold;
}

.grid-display {
  display: flex;
  align-content: flex-end;
  justify-content: flex-end;
  margin-left: 2vw;
  margin-right: 2vw;
  padding-left: 2vw;
  padding-top: 2vh;
  padding-right: 2vw;
  padding-bottom: 2vh;
}

.align-start {
  display: flex;
  align-content: flex-start;
  justify-content: flex-start;
  margin-left: 2vw;
}

.stationMenu {
  overflow: visible;
  filter: drop-shadow(0px 2px 2px rgba(0, 0, 0, 0.5));
  margin-top: 1.5px;
}
.stationMenu::before {
  position: absolute;
  top: 0;
  right: 14;
  width: 10;
  height: 10;
  /* transform: translateY(-50%) rotate(45deg); */
  z-index: 0;
}

.label-text {
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0em;
  padding-top: 0.5em;
  padding-bottom: 0em;
  /* text-align: left; */
}
#stations {
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 19px;
  letter-spacing: 0em;
}

#route-stations {
  font-family: Open Sans;
  font-size: 12px;
  font-weight: 700;
}

#station-buttons {
  padding: 0.725rem;
  margin: 0.5rem 0;
  margin-left: 1rem;
  font-weight: bold;
  width: 13rem;
}

/* Book trip Modal */
#dialog-title {
  font-family: Montserrat;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  height: 6vh;
  letter-spacing: 0em;
  text-align: center;
}

.center-botton-pading {
  display: flex;
  justify-content: center;
  align-items: center;
  /* text-align: center; */
  padding-bottom: 2rem;
  width: 100%;
}
.action-button {
  width: 8rem;
  display: inline-block;
  margin-left: 20px;
  margin-right: 20px;
  font-weight: bold;
}

.buttonText {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  letter-spacing: 0em;
  /* text-align: left; */
}

.timeClass {
  width: 12rem;
  margin-left: 1vw;
}

.flex-box {
  display: flex;
  flex-direction: row;
  margin-bottom: 1rem;
}

.seperator {
  font-family: Montserrat;
  font-size: 35px;
  font-weight: 10100;
  line-height: 24px;
  letter-spacing: 0em;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5em;
  padding-bottom: 0em;
}

#totes-box {
  display: flex;
  align-items: center;
  /* margin-bottom: 1.5rem; */
  align-self: center;
  align-content: center;
  /* margin-top: 10px; */
}

#totes-textfield {
  width: 10rem;
  margin-left: 1rem;
}

.book-trip-box1 {
  padding: 2rem !important;
}
.book-trip-box2 {
  width: 100%;
}
.book-trip-box3 {
