/* Recovery.css */
.header {
  height: 50vh;
  padding: 1rem;
  margin: 1rem;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}

.header-content {
  display: flex;
  justify-content: space-between;
  height: 10vh;
}

.header-title {
  padding: 1rem;
  height: 5vh;
}

.header-search {
  padding: 1rem;
  width: 20vw;
}

.mid-station-box {
  display: flex;
  flex-direction: column;
  height: 30vh;
  justify-content: center;
  align-items: center;
  row-gap: 1rem;
}

.mid-station-title {
  display: flex;
  justify-content: flex-start;
  padding: 0 2rem;
}

.stations-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  column-gap: 0.5rem;
  row-gap: 1rem;
}

.pagination {
  display: flex;
  height: 5vh;
  column-gap: 0.3rem;
  justify-content: center;
  align-items: center;
}

.pagination-button {
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 1;
  pointer-events: auto;
}

.pagination-button.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.page-indicators {
  display: flex;
}

.page-indicator {
  width: 10px;
  height: 10px;
  background-color: gray;
  border-radius: 50%;
  opacity: 0.7;
  display: inline-block;
  margin: 0 2px;
}

.page-indicator.active {
  background-color: black;
}

.bottom-buttons {
  display: flex;
  justify-content: flex-end;
  column-gap: 0.5rem;
}

.cancel-button {
  justify-content: flex-start;
}

.resume-button {
  align-content: right;
  align-items: center;
  justify-content: flex-end;
}


