.tabs {
  margin-left: 10rem;
  display: flex;
  justify-content: space-between;
}

.tabContainer {
  display: flex;
  align-items: center;
}

.addFleet {
  /* display: flex;
  justify-content: end; */
  display: flex;
  align-items: center;
  margin-top: 3%;
  margin-left: 24%;
  width: 50%;
}

.fleetsContainer {
  margin-top: 1rem;
  margin-bottom: 7rem;
  margin-left: 25%;
  width: 50%;
  max-height: 50%;
}

.card {
  padding: 1rem;
  margin-top: 1rem;
  display: flex;
  margin-right: 1rem;
}

.editButton {
  margin-right: 0.5rem !important;
  min-width: 0px !important;
}

.deleteIcon {
  width: 1rem;
  height: 1rem;
  margin-right: 0.3rem;
}

.addEditFleetHeading {
  font-weight: 900;
  padding-left: 5vh;
  height: 6vh;
  text-align: center;
}

.formContainer {
  margin-top: 2rem;
  justify-content: center;
  width: 100%;
}

.errorMsg {
  padding: 5px;
  font-size: 12px !important;
}

.submitButtonContainer {
  text-align: end;
  margin-top: 2rem;
}

.submitButtonStyle {
  padding: 1vh 2vw;
  width: 6vw !important;
}

.sherpasContainer {
  margin-top: 4rem;
  margin-bottom: 7rem;
  margin-left: 15%;
  width: 70%;
  max-height: 50%;
}

.addSherpaContainer {
  display: flex;
  align-items: center;
  margin-top: 3%;
  margin-left: 14%;
  width: 70%;
}

.addUser {
  display: flex;
  align-items: center;
  margin-top: 3%;
  margin-left: 9%;
  width: 80%;
}

.userName {
  margin-left: 1rem;
  text-align: center;
}

.sherpaName {
  margin-left: 1rem;
  text-align: center;
}

.errorMsgFleet {
  padding: 5px;
  font-size: 12px !important;
}

.fleetDropdown {
  margin-right: 2%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
