}
.notifications-list-wrapper {
  /* display: inline-flex; */
  padding-top: 20px;
  justify-content: center;
  align-items: center;
  background: #fafafa;
}
.notifications-popup-wrapper {
  padding: 25px;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0px 2.867088556289673px 2.867088556289673px 0px
    rgba(0, 0, 0, 0.25);
}

.no-data {
  padding: 5px 15px;
  border-radius: 10px;
  background: var(--secondary-colors-light-grey, #dbdbdb);
}

.popup-actions {
  color: var(--secondary-colors-blue, #0071c5);
  font-family: Open Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration-line: underline;
  cursor: pointer;
}
.list-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* padding-left: 0;
  padding-right: 0; */
  max-width: 100%;
  grid-gap: 10px;
  margin-top: 24px !important;
}
.menu-actions {
  display: flex;
  justify-content: right;
  align-items: center;
  margin: 1vh;
  margin-right: 50px;
}
.no-new-popup {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1vh;
}
.list-item {
  border-radius: 12px;
  background: #ffff;
  /* border: 1.0px solid var(--primary-colors-engineering-orange, #0a0a0a); */
}
.notification-box-shadow {
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25) !important;
}

.main-notifications-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.heading {
  margin-left: 5vh;
  font-size: 24px !important;
  font-style: normal;
  font-weight: 700 !important;
  line-height: normal;
}
.notifications-wrapper {
  display: flex;
  flex-direction: column;
  background: #fafafa !important;
}
.popup-heading {
  font-family: Montserrat;
  font-size: 21.503px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%; /* 32.255px */
  margin: 20px;
}
